
import React, { useState, useEffect } from 'react';
import icons from './icons';

const Journal = ({
    journals,
    setJournals,
    isCollapsed,
    journalGroupIndex,
    journalIndex,
    journal
}) => {

    const [collapsed, setCollapsed] = useState(journal.isCollapsed);
    const [edit, setEdit] = useState(false);
    const [editedJournal, setEditedJournal] = useState(null);
    const [editedJournalTitle, setEditedJournalTitle] = useState(null);

    const isEditedJournal = () => (edit) ? true : false;
    const toggleCollapse = () => {
        const collapse = !collapsed;
        const newJournals = [...journals];
        const selectedNewJournal = newJournals[journalGroupIndex].journals[journalIndex];
        selectedNewJournal.isCollapsed = collapse;
        setJournals(newJournals);
        setCollapsed(collapse);
    }
    const toggleEdit = () => {
        const toggle = (edit)
            ? false
            : true;
        const wasJournalTitleEdited = (journal.title !== editedJournalTitle) ? true : false;
        const wasJournalEdited = (journal.journal !== editedJournal) ? true : false;
        setEdit(toggle);
        setEditedJournal((toggle) ? journal.journal : '');
        setEditedJournalTitle((toggle) ? journal.title : '');
        if (!toggle && (wasJournalTitleEdited || wasJournalEdited)) {
            const newJournals = [...journals];
            const selectedNewJournal = newJournals[journalGroupIndex].journals[journalIndex];
            selectedNewJournal.description = (wasJournalTitleEdited) ? editedJournalTitle : selectedNewJournal.description;
            selectedNewJournal.journal = (wasJournalEdited) ? editedJournal : selectedNewJournal.journal;
            setJournals(newJournals);
        }
    }
    const deleteJournal = () => {
        const toggle = window.confirm(`Are you sure you want to remove journal: ${journal.description}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            const newJournals = [...journals];
            removeItemByIndex(newJournals[journalGroupIndex].journals, journalIndex);
            setJournals(newJournals);
        }
    }

    return <div key={`journal${journalIndex}`} className='containerBox lowerBorder'>
                <div className='containerBox contentLeft'>
                    <div className='containerBox flexContainer'>
                        <div className='flex1Auto contentLeft'>
                            {
                                (isEditedJournal())
                                    ? <textarea
                                        className='inputField ht-55 size20 r-10 bold color-lite'
                                        onChange={(e) => setEditedJournalTitle(e.target.value)}
                                        value={(editedJournalTitle !== null) ? editedJournalTitle : journal.description}
                                        placeholder={journal.description}
                                    >
                                        {journal.description}
                                    </textarea>
                                    : <div className='lh-20 bold size20'>{journal.description}</div>
                            }
                        </div>
                        <div
                            className={'rt-25 t-0 ml-5 r-10 size25 button contentRight bg-lite bold centeredContent'}
                            onClick={() => toggleCollapse()}
                        >
                            {
                                (!collapsed)
                                    ? <div>{icons.up}</div>
                                    : <div>{icons.down}</div>
                            }
                        </div>
                    </div>
                    {
                        (collapsed)
                        ? null
                        : <div>
                            <div className='containerBox'>
                                <div className='mt-10 color-soft'>
                                    {
                                        (isEditedJournal())
                                            ? <textarea
                                                className='inputField size20 r-10'
                                                onChange={(e) => setEditedJournal(e.target.value)}
                                                value={(editedJournal !== null) ? editedJournal : journal.journal}
                                                placeholder={editedJournal}
                                            >
                                                {editedJournal}
                                            </textarea>
                                            : journal.journal
                                    }
                                </div>
                            </div>
                            <div className='containerBox flexContainer contentRight'>
                                <div
                                    className={`rt-25 t-0 ml-5 mt-5 r-10 size15 button pl-20 contentRight ${(isEditedJournal()) ? '' : 'pt-10 pr-20 bg-lite'}`}
                                    onClick={() => toggleEdit(journalGroupIndex, journalIndex)}
                                >
                                    {
                                        (isEditedJournal())
                                            ? <div className='r-10 p-10 bg-neogreen color-dark bold'>save</div>
                                            : <div>{icons.edit}</div>
                                    }
                                </div>
                                <div 
                                    className='rt-25 t-0 ml-5 mt-5 r-10 size15 button pl-20 pb-10 contentRight pt-10 pr-20 bg-lite bold' 
                                    onClick={() => deleteJournal()}
                                >
                                    {icons.delete}
                                </div>
                            </div>
                        </div>
                    }
                </div>
            </div>
}
export default Journal;